<!--
 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-09-03 19:43:40
 * @LastEditTime: 2019-10-11 11:21:11
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="page-wrap">
    <!-- 顶部标题 -->
    <div class="headTitle">
      <i @click="toPrePage" class="cubeic-arrow"></i>
      <p>{{$t('firm_order_Period')}}</p>
    </div>
    <!-- 中间内容部分 -->
    <div class="contain_date">
      <div class="titleInfo">
        <p>WeLife Foreign Domestic Helper Insurance</p>
        <p>(For employer)</p>
      </div>
      <!-- 选择日期 -->
      <div class="select_date">
        <p>{{$t('hl_date_desc')}}</p>
        <div class="departureDate">
          <div class="leftText">{{$t('products_departure_data')}}</div>
          <div @click="showDatePicker" class="rightDate">{{date.departureDate}}</div>
        </div>
        <div class="returnDate">
          <div class="leftText">{{$t('products_return_data')}}</div>
          <div class="rightDate">{{date.returnDate}}</div>
        </div>
      </div>
    </div>
    <!-- 底部按钮 -->
    <div class="button-bar">
      <button @click="confirm" class="proceed">{{$t('info_doc_data_next')}}</button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      date: {
        returnDate: "XXXX/XX/XX",
        departureDate: ""
      },
      todayDate: "",
      isMore: ""
    };
  },
  methods: {
    ...mapMutations(["setPolicyDate"]),
    confirm() {
      if (!this.date.returnDate) {
        this.$createToast({
          txt: this.$t("insurance_toast"),
          time: 3000,
          type: "txt"
        }).show();
        return false;
      }
      //判断出发日期是否小于当天
      else if (
        new Date(`${this.date.departureDate} 00:00:00`).getTime() <
        new Date(`${this.todayDate} 00:00:00`).getTime()
      ) {
        this.$createToast({
          txt: this.$t("travel_annaul_toast"),
          time: 3000,
          type: "txt"
        }).show();
        return false;
      }
      //判断年计划的出发日期是否大于距离当天有180天
      else if (this.isMore > 180) {
        this.$createToast({
          txt: this.$t("moreDays"),
          time: 3000,
          type: "txt"
        }).show();
        return false;
      }
      console.log("点击了");
      this.setPolicyDate(this.date);
      this.$router.push("/Employer");
    },
    toPrePage() {
      this.$router.push('/Prompt');
    },
    //点击选择时间日期的事件,更加type來判斷是出發日期還是回程日期
    showDatePicker() {
      if (!this.datePicker) {
        this.datePicker = this.$createDatePicker({
          title: "Date Picker",
          min: new Date(2008, 7, 8),
          max: new Date(2020, 9, 20),
          value: new Date(),
          cancelTxt: this.$t("cancel"),
          confirmTxt: this.$t("confirm"),
          onSelect: this.selectHandle,
          onCancel: this.cancelHandle,
          onChange: this.changeHandle
        });
      }
      this.datePicker.show();
    },
    //点击时间日期插件确认键事件
    selectHandle(date, selectedVal, selectedText) {
      console.log(date, selectedVal, selectedText);
      this.date.departureDate = this.dateTozero(selectedVal).join("/");
      let dateArr = this.dateTozero(selectedVal);
      //如果是选择一年计划,从新选择时间,returndate会相应改变
      if (this.plan.period_type == 1) {
        this.date.returnDate = [
          (Number(dateArr[0]) + 1).toString(),
          dateArr[1],
          dateArr[2] - 1 < 10 ? "0" + (dateArr[2] - 1) : dateArr[2] - 1
        ].join("/");
      } else {
        //如果是选择两年计划,从新选择时间,returndate会相应改变
        this.date.returnDate = [
          (Number(dateArr[0]) + 2).toString(),
          dateArr[1],
          dateArr[2] - 1 < 10 ? "0" + (dateArr[2] - 1) : dateArr[2] - 1
        ].join("/");
      }
      this.fromToday()
    },
    //数字小于10时前面加个0
    dateTozero(arr) {
      return arr.map(item => (item < 10 ? "0" + item : "" + item));
    },
    //点击时间日期插件取消键事件
    cancelHandle() {},
    // 获取当天的天数
    getToday(date) {
      var y = date.getFullYear();
      var m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      var d = date.getDate();
      d = d < 10 ? "0" + d : d;
      return y + "/" + m + "/" + d;
    },
    //获取到一年后的日期
    getNextToday(date) {
      var y = date.getFullYear() + 1;
      var m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      var d = date.getDate() - 1;
      d = d < 10 ? "0" + d : d;
      return y + "/" + m + "/" + d;
    },
    //获取到一年后的日期
    getTwoYear(date) {
      var y = date.getFullYear() + 2;
      var m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      var d = date.getDate() - 1;
      d = d < 10 ? "0" + d : d;
      return y + "/" + m + "/" + d;
    },
    //计算出发时间距离当天有多少天
    fromToday() {
      console.log(new Date(`${this.todayDate} 00:00:00`).getTime());
      console.log(new Date(`${this.date.departureDate} 00:00:00`).getTime());
      this.isMore =
        Math.ceil(
          (new Date(`${this.date.departureDate} 00:00:00`).getTime() -
            new Date(`${this.todayDate} 00:00:00`).getTime()) /
            (1000 * 60 * 60 * 24)
        ) + 1;
      console.log(this.isMore);
    }
  },
  created() {
    //获取当天的天数
    this.todayDate = this.getToday(new Date());
    this.date.departureDate = this.getToday(new Date());
    if (this.plan.period_type == 1) {
      this.date.returnDate = this.getNextToday(new Date());
    } else {
      this.date.returnDate = this.getTwoYear(new Date());
    }
  },
  computed: {
    ...mapState(["plan"])
  }
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.page-wrap {
  padding: 34px;
  background: @background;
  padding-bottom: 260px;
  position: relative;
  padding-top: 120px;

  .button-bar {
    margin: 0 auto;
    text-align: center;
    position: fixed;
    width: 100%;
    height: 200px;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    border-top: 3px solid rgba(0, 0, 0, 0.25);
    background-color: rgba(250, 250, 250);
  }
  .proceed {
    width: 350px;
    height: 100px;
    line-height: 100px;
    margin-top: 50px;
    border: none;
    border-radius: 10px;
    background: rgb(2, 164, 71);
    color: #fff;
    text-align: center;
    font-size: 36px;
  }
  .headTitle {
    position: fixed;
    height: 100px;
    top: 0;
    width: 100%;
    left: 0;
    z-index: 99;
    background-color: rgba(250, 250, 250);
    border-bottom: 2px solid rgba(0, 0, 0, 0.25);
    .cubeic-arrow {
      color: rgb(0, 122, 255);
      display: inline-block;
      transform: rotateZ(180deg);
      font-size: 32px;
      font-weight: bold;
      position: absolute;
      left: 0px;
      line-height: 100px;
      width: 50px;
      text-align: center;
      height: 100%;
    }
    p {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      display: inline-block;
      font-size: 30px;
      font-weight: 600;
      margin-top: 40px;
    }
  }
  .contain_date {
    padding: 20px 25px;
    .titleInfo {
      text-align: center;
      font-size: 30px;
      p {
        color: rgb(12, 129, 255);
        font-weight: bold;
      }
    }
    .select_date {
      margin-top: 100px;
      p {
        font-size: 30px;
        font-weight: bold;
        margin-bottom: 50px;
      }
      .departureDate {
        height: 70px;
        background-color: #ccc;
        border-radius: 35px;
        padding: 0 30px;
        display: flex;
        justify-content: space-between;
      }
      .returnDate {
        height: 70px;
        background-color: #ccc;
        border-radius: 35px;
        margin-top: 10px;
        padding: 0 30px;
        display: flex;
        justify-content: space-between;
      }
      .leftText {
        color: rgb(12, 129, 255);
        line-height: 70px;
        font-size: 28px;
      }
      .rightDate {
        display: inline-block;
        font-size: 30px;
        line-height: 70px;
        margin-right: 50px;
        color: rgb(74, 74, 74);
      }
    }
  }
}
</style>


